import React,{Component} from 'react'; 
import { Button ,Input} from 'antd';
import axios from 'axios';
import globalConfig from '../config'
import 'antd/dist/antd.css';
import '../css/channel.css';
/*===================================*/ 
// import globalConfig from '../config'
/*=====================================*/ 

export default class Channel extends Component{
    constructor(props){
        super(props);
        this.state = {
            channelVal: '',//
            tips: '',//
            tipsClass: 'tips',//
        }
    }
    
    channelChange = (ev)=>{
        this.setState({
            channelVal: ev.target.value
        })
        console.log(globalConfig);
        // console.log(this.state.channelVal);
    }
    //
    newBtn = () =>{
        // console.log(this.state.channelVal);
        let channelVal = this.state.channelVal;
        if(channelVal !== ""){
            axios.get(globalConfig.setApiUrl('/newChannel/newChannelName'),{
                params: {
                    channel_val: channelVal
                  }
            }).then((response) => {
                console.log(response.data)
                if (response.data === 'yes') {
                    this.setState({
                        tipsClass: 'tips_wrong',
                        tips: ''
                    })
        
                } else if (response.data === 'ok') {
                    this.setState({
                        tipsClass: 'tips_right',
                        tips: ''
                    })
        
                }
            });
        }else{
            this.setState({
                tipsClass: 'tips_wrong',
                tips: ''
            })

        }

    }
    //
    resetBtn =(ev) => {
        this.setState({
            channelVal: '',
            tipsClass: 'tips',
            tips: ''

        })
    }

    render(){
        return (
            <form>
                <p></p>
                <Input size="large" 
                placeholder="" 
                className="channel" 
                value={this.state.channelVal}
                onChange={this.channelChange}
                />
                <p className={this.state.tipsClass}>{this.state.tips}</p>
                <Button 
                type="primary" 
                className="newBtn" 
                onClick={this.newBtn.bind(this)}
                ></Button>
                <Button 
                type="primary" 
                className="resetBtn" 
                onClick={this.resetBtn.bind(this)}
                ></Button>
            </form>
        )
    }
}