import React,{Component} from 'react';
import '../css/template.css'
import axios from 'axios';
import { Icon } from 'antd';
/*===============================================*/ 
import globalConfig from '../config'
/*===============================================*/ 
export default class NewTemplate2 extends Component{
    constructor(props) {
        super(props);
        this.state = {
            modleNum: '',//
            urls: '',//
            channelArr: [],
            channelVal: '',
            appNameArr: ['----'],
            appNameVal: '',
            pageNameArr: [],
            pageNameVal: '',//
            tips: '',//
            icons: '',//
            tisi: '',//
            dis: true,
            Unified_logo: 'none',//logo
            Distinguish_logo: 'none',//logo
            Unified_appName: 'none',//appName
            Distinguish_appName: 'none',//appName
            config_nums: [],//
            chapter_nums: [],//
        }
    }

    //
    goBack(){
        this.props.history.push('/landPage');
    }

    //
    channelChange = (ev)=>{
        this.setState({
        channelVal: ev.target.value
        })
        let channelVal = ev.target.value;
        if(channelVal !== '----'){
        //
        axios.get(globalConfig.setApiUrl('/newChannel/selectObj'),{
            params: {
            select_text: channelVal
            }
        }).then((response) =>{
            console.log(response.data);
            let arrs = response.data;
            let str = '----';
            arrs.unshift(str);
            for(let i = 0 ;i< response.data.length;i++){
            this.setState({
                appNameArr: arrs,
                tips: '',
                icons: '',
            })
            }
        })
        }else{
        this.setState({
            tips: '',
            appNameArr: ['----'],
            dis:true,
            icons: <Icon type="exclamation-circle" theme="twoTone" twoToneColor="#DC143C"/>,//
            tisi: '',
            pageNameVal: ''
        })
        }
    }

    //
    appNameChange = (ev) =>{
        this.setState({
        appNameVal: ev.target.value
        })
        let appNameVal = ev.target.value
        if(appNameVal === '----'){
        this.setState({
            tips: '',
            dis: true,
            icons: <Icon type="exclamation-circle" theme="twoTone" twoToneColor="#DC143C"/>,//
            tisi: '',
            pageNameVal: ''
        })
        }else{
        this.setState({
            tips: '',
            dis:false,
            tisi: '',
            icons: ''
        })
        }
    }

    //
    pageNameChange = (ev) =>{
        this.setState({
        pageNameVal: ev.target.value
        })
    }

    //action
    actions = ()=>{
        let channel = this.state.channelVal;
        let appName = this.state.appNameVal;
        let pageName = this.state.pageNameVal;
        if(channel !== '----' && appName !== '----' && pageName !==''){
            axios.get(globalConfig.setApiUrl('/historyDirectory/isDirectory'),{
                params: {
                    channelName: channel,
                    appName: appName,
                    pageNum: pageName
                }
            }).then((response)=>{
                if(response.data === 'yes'){
                    this.setState({
                        icons: <Icon type="exclamation-circle" theme="twoTone" twoToneColor="#DC143C"/>,//
                        tisi: '',//   
                    })
                }else if(response.data === 'no'){
                    this.setState({
                        icons: <Icon type="check-circle" theme="twoTone" />,//
                        tisi: '',//
                        urls: globalConfig.setApiUrl('/modles/modles2')+'?pageNum=' + pageName +'&channelName=' + channel + '&appName='+ appName 
                    })
                }
            })
        }
    }

    //logo
    select_logo = (ev)=>{
        console.log(ev.target.value);
        let logos = ev.target.value;
        if(logos === '1'){
            this.setState({
                Unified_logo: 'block',//logo
                Distinguish_logo: 'none',//logo
            })
        }else{
            this.setState({
                Unified_logo: 'none',//logo
                Distinguish_logo: 'block',//logo
            })
        }
    }

    //
    add_config_box =() => {
        let newItem = 1;
        this.setState({
            config_nums: [...this.state.config_nums, newItem]
        })

    }
    //
    del_config_box = ()=>{
        this.setState({
            config_nums: this.state.config_nums.slice(0, this.state.config_nums.length - 1)
        })
    }

    //
    add_chapter_box = ()=>{
        let num = 1;
        this.setState({
            chapter_nums: [...this.state.chapter_nums,num]
        })
    }
    //
    del_chapter_box = ()=>{
        this.setState({
            chapter_nums: this.state.chapter_nums.slice(0,this.state.chapter_nums.length-1)
        })
    }
    //app
    select_appName = (ev)=>{
        console.log(ev.target.value);
        let appName = ev.target.value;
        if(appName === '1'){
            this.setState({
                Unified_appName: 'block',//appName
                Distinguish_appName: 'none',//appName
            })
        }else{
            this.setState({
                Unified_appName: 'none',//appName
                Distinguish_appName: 'block',//appName
            })
        }
    }

    //
    resetBtn = ()=>{
        this.setState({
            Unified_logo: 'none',//logo
            Distinguish_logo: 'none',//logo
            Unified_appName: 'none',//appName
            Distinguish_appName: 'none',//appName
            pageNameVal:'',//
            icons: '',//
            tisi: '',//
            dis: true,//
        })
    }


    /**/
    componentDidMount(){
        //
        axios.get(globalConfig.setApiUrl('/newPageName/getChannel')).then((response) =>{
        console.log(response.data);
        let arrs = response.data;
        let str = '----';
        arrs.unshift(str);
        for(let i = 0 ;i< response.data.length;i++){
            this.setState({
            channelArr: arrs
            })
        }
        })

        //
        let str = (this.props.history.location.search).substr(1);
        let strArr = str.split('=');
        this.setState({
            modleNum: strArr[strArr.length-1]
        })
    } 
    render() {
        return (
            <form id="newCeatePage" method="post" encType="multipart/form-data" className="template1From"  action={this.state.urls}>
                <i className="goBack" onClick={this.goBack.bind(this)}></i>
                <div className="modle_num">
                <p>id</p>
                <div>
                    <input type="text" name="modle_num" value={this.state.modleNum} disabled/>
                </div>
                </div>
                <div className="channel_pageName">
                    <p></p>
                    <select name="channel_name" id="channel_name" value={this.state.channelVal} onChange={this.channelChange.bind(this)}>
                        {this.state.channelArr.map((item,index)=>{
                            return (
                                    <option key={index}>{item}</option>
                                )
                        })}
                    </select>
                    <select name="pageName_name" id="pageName_name" value={this.state.appNameVal} onChange={this.appNameChange.bind(this)}>
                        {this.state.appNameArr.map((item,index)=>{
                            return (
                                <option key={index}>{item}</option>
                            )
                        })}
                    </select>
                    <span>{this.state.tips}</span>
                </div>
                <div className="pageNum">
                    <p></p>
                    <div>
                        <input
                            disabled={this.state.dis}
                            type="text" 
                            name="pageNum" 
                            autoComplete="off" 
                            required min="0" 
                            value={this.state.pageNameVal}
                            onChange={this.pageNameChange.bind(this)}
                            onBlur={this.actions.bind(this)}
                            />
                        <span className="tisi"></span>
                    </div>
                    <span>
                        {this.state.icons}{this.state.tisi}
                    </span>
                </div>
                <div className="select_logo">
                    <p>logo</p>
                    <input type="radio" name="logos" value="1" onChange={this.select_logo.bind(this)} required/>
                    <span>logo</span>
                    <input type="radio" name="logos" value="2" onChange={this.select_logo.bind(this)} required/>
                    <span>logo</span>
                </div>
                <div id="Unified_logo" style={{display:this.state.Unified_logo}}>
                    <div className="app_logo" >
                        <p>applogo</p>
                        <div>
                            <input type="file" name="app_logo" autoComplete="off" required={this.state.Unified_logo==="block"?true:false}/>
                        </div>
                    </div>
                </div>
                <div id="Distinguish_logo"  style={{display:this.state.Distinguish_logo}}>
                    <div className="android_logo">
                        <p>logo</p>
                        <div>
                            <input type="file" name="android_logo" autoComplete="off" required={this.state.Distinguish_logo==="block"?true:false}/>
                        </div>
                    </div>
                    <div className="ios_logo">
                        <p>ioslogo</p>
                        <div>
                            <input type="file" name="ios_logo" autoComplete="off" required={this.state.Distinguish_logo==="block"?true:false}/>
                        </div>
                    </div>
                </div>
                <div className="select_appName">
                    <p>app</p>
                    <input type="radio" name="names" value="1" onChange={this.select_appName.bind(this)} required/>
                    <span></span>
                    <input type="radio" name="names" value="2"onChange={this.select_appName.bind(this)} required/>
                    <span></span>
                </div>
                <div className="Unified_appName" style={{display: this.state.Unified_appName}}>
                    <p>app</p>
                    <div>
                        <input type="text" name="appName" autoComplete="off" required={this.state.Unified_appName==="block"?true:false}/>
                    </div>
                </div>
                <div className="Distinguish_appName" style={{display: this.state.Distinguish_appName}}>
                    <div className="android_appName">
                        <p>-app</p>
                        <div>
                            <input type="text" name="android_appName" autoComplete="off" required={this.state.Distinguish_appName==="block"?true:false}/>
                        </div>
                    </div>
                    <div className="ios_appName">
                        <p>ios-app</p>
                        <div>
                            <input type="text" name="ios_appName" autoComplete="off" required={this.state.Distinguish_appName==="block"?true:false}/>
                        </div>
                    </div>
                </div>
                    <div className="book_name">
                    <p></p>
                    <div>
                        <input type="text" name="book_name" autoComplete="off" required/>
                    </div>
                    </div>
                    <div className="book_introduce">
                    <p></p>
                    <div>
                        <textarea name="book_introduce" required></textarea>
                    </div>
                </div>
                <div className="app_downNum">
                    <p>app</p>
                    <div>
                        <input type="number" name="app_downNum" autoComplete="off" required min="0"/>
                    </div>
                </div>
                <div className="book_imgs">
                    <p></p>
                    <div>
                        <input type="file" name="book_imgs" autoComplete="off" required/>
                    </div>
                </div>
                <div className="chapter_box">
                    <div className="chapter_name">
                        <p></p>
                        <div>
                            <input type="text" name="chapter_name" autoComplete="off" required min="0"/>
                        </div>
                    </div>
                    <div className="chapter_content">
                        <p></p>
                        <div>
                            <textarea name="chapter_content"></textarea>
                        </div>
                    </div>
                </div>
                {this.state.chapter_nums.map((item,index)=>{
                    return (
                        <div className="chapter_box" key={index}>
                            <div className="chapter_name">
                                <p>{index+2}</p>
                                <div>
                                    <input type="text" name="chapter_name" autoComplete="off" required min="0"/>
                                </div>
                            </div>
                            <div className="chapter_content">
                                <p>{index+2}</p>
                                <div>
                                    <textarea name="chapter_content"></textarea>
                                </div>
                            </div>
                            <i className="del_chapter_box" onClick={this.del_chapter_box.bind(this)}></i>
                        </div>
                    )
                })}
                <div className="add_chapter" onClick={this.add_chapter_box.bind(this)}></div>
                <div className="book_info">
                    <p>()</p>
                    <div>
                        <input type="text" name="book_info" autoComplete="off" required/>
                    </div>
                </div>

                <div className="app_down">
                    <p>app</p>
                    <div>
                        <input type="text" name="app_down" autoComplete="off" required/>
                    </div>
                </div>
                <div id="config_box">
                    <div className="android_downLink">
                        <p></p>
                        <div>
                            <input type="text" name="android_downLink" autoComplete="off" required/>
                        </div>
                    </div>
                    <div className="ios_downLink">
                        <p>ios</p>
                        <div>
                            <input type="text" name="ios_downLink" autoComplete="off" required/>
                        </div>
                    </div>
                    <div className="app_hostname">
                        <p></p>
                        <div>
                            <input type="text" name="app_hostname" autoComplete="off" required/>
                        </div>
                    </div>
                    <div className="app_copyright">
                        <p></p>
                        <div>
                            <input type="text" name="app_copyright" autoComplete="off" required/>
                        </div>
                    </div>
                    <div className="copyright_logo">
                        <p>logo</p>
                        <div>
                            <input type="file" name="copyright_logo" autoComplete="off"/>
                        </div>
                    </div>
                </div>
                {this.state.config_nums.map((item,index)=>{
                    return (
                        <div id="config_box" key={index+1}>
                            <div className="android_downLink">
                                <p>{index+2}</p>
                                <div>
                                    <input type="text" name="android_downLink" autoComplete="off" required/>
                                </div>
                            </div>
                            <div className="ios_downLink">
                                <p>ios{index+2}</p>
                                <div>
                                    <input type="text" name="ios_downLink" autoComplete="off" required/>
                                </div>
                            </div>
                            <div className="app_hostname">
                                <p>{index+2}</p>
                                <div>
                                    <input type="text" name="app_hostname" autoComplete="off" required/>
                                </div>
                            </div>
                            <div className="app_copyright">
                                <p>{index+2}</p>
                                <div>
                                    <input type="text" name="app_copyright" autoComplete="off" required/>
                                </div>
                            </div>
                            <div className="copyright_logo">
                                <p>logo{index+2}</p>
                                <div>
                                    <input type="file" name="copyright_logo" autoComplete="off"/>
                                </div>
                            </div>
                            <i className="del_config_box" onClick={this.del_config_box.bind(this)}></i>
                        </div>
                    )
                })}
                <div className="add_config" onClick={this.add_config_box.bind(this)}></div>
                <input type="submit" value="" id="submit_Btn"/>
                <input type="reset" value="" id="reset_btn"  onClick={this.resetBtn.bind(this)}/>
            </form>
        )
    }
}